import './ff-textarea.scss';

/**
 * ffTextarea directive is a wrapper for standard textarea tag, adds some styling.
 */
angular.module('flexForms.ffTextarea', [
  'deskworks.ffControlController',
  'deskworks.ffReadOnlyIgnore',
])

  .directive('ffTextarea', function($compile, $templateCache, ffHelper) {
    return {
      restrict: 'E',
      scope: true,
      require: {
        ffBulkReadOnly: '^?ffBulkReadOnly',
        ffReadOnlyIgnore: '^?ffReadOnlyIgnore',
        ffBlock: '^^?ffBlock',
        ffControl: 'ffTextarea',
      },
      controller: 'ffControlController as ffControl',
      bindToController: { },

      link: function($scope, $element, $attrs, $ctrls) {
        const ffControl = $ctrls.ffControl;
        const $input = angular.element($templateCache.get('ffTextarea'));
        const $static = $compile($templateCache.get('ffTextStatic'))($scope);

        ffHelper.copyInnerAttrs($element, $attrs, $input);
        $element.empty().append($input);
        $compile($input)($scope);

        const rowspan = $scope.$eval($attrs.rowspan);

        if (rowspan) {
          $element.css('min-height', 2.4 * rowspan + 'rem'); // 2.4rem = $ff-control-min-height
        }

        ffControl.init({ renderEdit: renderEdit, renderView: renderView, modelToStatic: modelToStatic });

        function renderEdit() {
          $static.detach();
          $element.append($input);
        }
        function renderView() {
          $input.detach();
          $element.append($static);
        }
        function modelToStatic(model) {
          return model;
        }
      },
    };
  });
