/**
 * Media query styling directive: <div mqs="{ sm: {flex: '0 0 50%'}, md: {flex: '0 0 33%'} }">
 */
angular.module('mqs', [])
  .directive('mqs', function(mqsClasses, helper) { 'use strict';
    return {
      restrict: 'A',
      link: function ($scope, $element, $attrs) {
        $attrs.$observe('mqs', function(param) {
          param = $scope.$eval(param);
          const classes = mqsClasses(param).join(' ');
          $element.addClass(classes);
        });
      },
    };
  })

  .factory('mqsBreakpoints', function() { 'use strict';
    const breakpoints = [
      { id: 'xs', width: null },
      { id: 'sm', width: '480px' },
      { id: 'md', width: '640px' },
      { id: 'lg', width: '768px' },
      { id: 'xl', width: '1024px' },
    ];

    breakpoints.forEach(bp => {
      const style = document.createElement('style');
      if(bp.width) style.setAttribute('media', `(min-width: ${bp.width})`);
      // style.appendChild(document.createTextNode('')); // WebKit hack :(
      document.head.appendChild(style);
      bp.sheet = style.sheet;
    });

    return breakpoints;
  })

  // Converts mqs directive parameter to list of CSS classes and dynamically creates these classes
  .factory('mqsClasses', function(mqsBreakpoints) { 'use strict';
    const classes = {}; // class names cache

    return function(param) {
      return mqsBreakpoints
        .map(bp => {
          const styles = param[bp.id];
          if(!styles) return null;

          return Object.keys(styles)
            .map(styleName => {
              const styleVal = '' + styles[styleName];
              const className = `${bp.id}_${styleName}_${styleVal.replace(/(?: |\.)/g, '_').replace(/%/g, 'pct')}`;
              if(angular.isUndefined(classes[className])) {
                classes[className] = bp.sheet.cssRules.length;
                bp.sheet.insertRule(`.${className} { ${styleName}: ${styleVal} !important; }`, bp.sheet.cssRules.length);
              }
              return className;
            });
        })
        .filter(className => !!className)
        .reduce((result, item) => result.concat(item), []);
    };
  });
