export default angular.module('flexForms', [
  'mqs',
  'flexForms.flexForm',
  'flexForms.flexFormTitle',
  'flexForms.ffPanel',
  'flexForms.flex',
  'flexForms.ffRow',
  'flexForms.ffLabel',
  'flexForms.ffStatic',
  'flexForms.ffText',
  'flexForms.ffUrl',
  'flexForms.ffLogin',
  'flexForms.ffPassword',
  'flexForms.ffEmail',
  'flexForms.ffTime',
  'flexForms.ffDate',
  'flexForms.ffDatepicker',
  'flexForms.ffDatepickerPopup',
  'flexForms.ffNumber',
  'flexForms.ffPrice',
  'flexForms.ffSelect',
  'flexForms.ffTextarea',
  'flexForms.ffCheckbox',
  'flexForms.ffRadio',
  'flexForms.ffBtn',
  'flexForms.ffBtnGroup',
  'flexForms.ngValidateAlert',
  'flexForms.ffSpin',
  'flexForms.ffDim',
  'flexForms.ffImg',
  'flexForms.ffSearch',
  'flexForms.validation.ngTimeOrder',
  'flexForms.validation.ngDateOrder',
  'flexForms.validation.ngDateDiff',
  'flexForms.validation.ngUnique',
  'flexForms.validation.ngLte',
  'flexForms.validation.ngLt',
  'flexForms.validation.ngGte',
  'flexForms.validation.ngGt',
  'flexForms.validation.ngMatch',
  'flexForms.validation.ngNotContain',
  'flexForms.validation.ngCustom',
  'flexForms.ngCollapse',
  'flexForms.ffPagination',
  'flexForms.ffIcon',
  'flexForms.ffInfo',
  'flexForms.ffSection',
  'flexForms.ffTip',
  'flexForms.ffHint',
  'flexForms.ffCountdown',
  'flexForms.ffIncludeAll',
  'flexForms.ffSort',
  'flexForms.ffCcNumber',
  'flexForms.ffBulkReadOnly'
])

  .factory('ffHelper', function() {
    return {
      /**
       * Simplified version of copyInnerAttrs, used in ffBtn directive
       * @param src - source element
       * @param dst - destination element
       * @returns {Array} - list of attributes names to remove from parent element on next digest
       */
      copyAttrs: function(src, dst) {
        const attrsToCopy = ['name', 'minlength', 'maxlength', 'placeholder', 'ff-countdown', 'ta-toolbar'];
        const attrsToRemove = [];

        for (const attr of src[0].attributes) {
          const { name, value } = attr;

          if (name.indexOf('ff-') === 0 && name !== 'ff-countdown' && name !== 'ff-alt' && name !== 'ff-spin') {
            dst.attr('ng-' + name.substr(3), value);
          } else if (name.indexOf('aria-') === 0 || attrsToCopy.indexOf(name) >= 0) {
            dst.attr(name, value);
            attrsToRemove.push(name);
          }
        }
        return attrsToRemove;
      },

      /**
       * Copies ff-prefixed attributes from directive root element to specified child attributes receiver with ng-prefix.
       * Should be used in compile function to copy attributes on compile stage (template level), before linking stage (instances level).
       * @param tElement - first parameter of compile function
       * @param tAttrs - second parameter of compile function
       * @param attrsReceiver - child element, attributes receiver
       */
      copyInnerAttrs: function(tElement, tAttrs, attrsReceiver) {
        for (const key in tAttrs.$attr) {
          const attr = tAttrs.$attr[key];
          if (attr.indexOf('ff-') === 0 && attr !== 'ff-countdown' && attr !== 'ff-alt' && attr !== 'ff-spin') {
            attrsReceiver.attr('ng-' + attr.substr(3), tAttrs[key]);
          } else if (attr.indexOf('aria-') === 0) {
            attrsReceiver.attr(attr, tAttrs[key]);
            tAttrs.$set(attr, null);
          }
        }

        if (tAttrs.name) { // copy name attribute and remove it
          attrsReceiver.attr('name', tAttrs.name);
          tAttrs.$set('name', null);
        }

        attrsReceiver.attr('minlength', tAttrs.minlength);
        tAttrs.$set('minlength', null);

        attrsReceiver.attr('maxlength', tAttrs.maxlength);
        tAttrs.$set('maxlength', null);

        attrsReceiver.attr('placeholder', tAttrs.placeholder);
        tAttrs.$set('placeholder', null);

        attrsReceiver.attr('ff-countdown', tAttrs.ffCountdown);
        tAttrs.$set('ffCountdown', null);

        attrsReceiver.attr('ta-toolbar', tAttrs.taToolbar);
        tAttrs.$set('taToolbar', null);

        return attrsReceiver;
      },

      /**
       * Checks if we are running on a mobile platform
       * @returns {boolean} - true if on mobile platform, false otherwise
       */
      isMobile: function isMobile() {
        if (navigator.userAgent && (navigator.userAgent.match(/Android/i) ||
            navigator.userAgent.match(/webOS/i) ||
            navigator.userAgent.match(/iPhone/i) ||
            navigator.userAgent.match(/iPad/i) ||
            navigator.userAgent.match(/iPod/i) ||
            navigator.userAgent.match(/BlackBerry/i) ||
            navigator.userAgent.match(/Windows Phone/i))) {
          return true;
        }
      }

    };
  });
