/**
 * ffCheckbox directive - wrapper for standard input type="checkbox"
 */
angular.module('flexForms.ffCheckbox', [
  'deskworks.ffControlController',
  'deskworks.ffReadOnlyIgnore',
])

  .directive('ffCheckbox', function($compile, $templateCache, ffHelper) { 'use strict';
    return {
      restrict: 'E',
      scope: true,
      require: {
        ffBulkReadOnly: '^?ffBulkReadOnly',
        ffReadOnlyIgnore: '^?ffReadOnlyIgnore',
        ffBlock: '^^?ffBlock',
        ffControl: 'ffCheckbox',
      },
      controller: 'ffControlController as ffControl',
      bindToController: { },

      compile: function(tElement, tAttrs) {
        const innerHtml = tElement.html();
        tElement.empty();

        return function link($scope, $element, $attrs, $ctrls) {
          const ffControl = $ctrls.ffControl;
          const $input = angular.element($templateCache.get('ffCheckbox'));
          const $i = angular.element('<i></i>');
          const $static = $compile($templateCache.get('ffCheckboxStatic'))($scope);

          let $label = $attrs.ffLabel || innerHtml.trim();
          if ($label) $label = angular.element('<span class="cb-label"></span>').append($label);

          ffHelper.copyInnerAttrs($element, $attrs, $input);
          $element.empty().append($input).append($i);
          $compile($input)($scope);

          if ($label) {
            $element.append($label);
            $compile($label)($scope);
          }

          ffControl.init({ renderEdit, renderView, modelToStatic });

          function renderEdit() {
            $static.detach();
            $element.prepend($input);
          }
          function renderView() {
            $input.detach();
            $element.prepend($static);
          }
          function modelToStatic(model) {
            if (angular.isDefined($attrs.ffTrueValue) && model == $attrs.ffTrueValue) return true;
            if (angular.isDefined($attrs.ffFalseValue) && model == $attrs.ffFalseValue) return false;
            return !!model;
          }
        };
      },
    };
  });
