/**
 * ffBtn directive - wrapper for standard button element.
 * Supports coloring and iconing by sense attribute and sizing by size attribute.
 */
export default angular.module('flexForms.ffBtn', [])
  .directive('ffBtn', function($rootScope, $q, $compile, $timeout, ffHelper) {
    // Size -> color classes
    const sizeSizeClasses = {
      xs: 'btn-xs',
      sm: 'btn-xs',
      lg: ''
    };

    // Sense -> color/icon classes
    const presets = {
      edit: { color: 'btn-primary', icon: 'fa-pencil' },
      add: { color: 'btn-success', icon: 'fa-plus' },
      'add-alt': { color: 'btn-primary', icon: 'fa-plus' },
      new: { color: 'btn-success', icon: 'fa-plus' },
      view: { color: 'btn-success', icon: 'fa-eye' },
      clone: { color: 'btn-success', icon: 'fa-copy' },
      back: { color: 'btn-warning', icon: 'fa-chevron-left' },
      import: { color: 'btn-success', icon: 'fa-upload' },
      remove: { color: 'btn-danger', icon: 'fa-remove' },
      delete: { color: 'btn-danger', icon: 'fa-remove' },
      save: { color: 'btn-success', icon: 'fa-check' },
      next: { color: 'btn-success', icon: 'fa-chevron-right' },
      search: { color: 'btn-primary', icon: 'fa-search' },
      userSelect: { color: 'btn-success', icon: 'fa-user' },
      reserve: { color: 'btn-success', icon: 'fa-check' },
      ok: { color: 'btn-success', icon: 'fa-check' },
      cancel: { color: 'btn-warning', icon: 'fa-ban' },
      upload: { color: 'btn-primary', icon: 'fa-upload' },
      download: { color: 'btn-primary', icon: 'fa-download' },
      favorite: { color: 'btn-success', icon: 'fa-star' },
      makeFavorite: { color: 'btn-warning', icon: 'fa-star-o' },
      collapse: { color: 'btn-primary', icon: 'fa-angle-down' },
      collapseBtn: { color: 'btn-success', icon: 'fa-angle-down' },
      collapseBtnPriv: { color: 'btn-success', icon: 'fa-angle-down' }, // privileged - hidden when read-only
      signup: { color: 'btn-primary' },
      link: { color: 'btn-link' },
      checkIn: { color: 'btn-success', icon: 'fa-check' },
      checkOut: { color: 'btn-warning', icon: 'fa-check' },
      home: { color: 'btn-warning', icon: 'fa-home' },
      charge: { color: 'btn-primary', icon: 'fa-dollar' },
      merge: { color: 'btn-success', icon: 'fa-copy' },
      help: { color: 'btn-primary', icon: 'fa-question' },
      reset: { color: 'btn-warning', icon: 'fa-undo' },
      calendar: { color: 'btn-warning', icon: 'fa-calendar' },
      close: { color: 'btn-link', icon: 'fa-close' },
      signOut: { color: 'btn-primary', icon: 'fa-sign-out' },
      settings: { color: 'btn-primary', icon: 'fa-cog' },
      crop: { color: 'btn-primary', icon: 'fa-crop' },
      expand: { color: 'btn-primary', icon: 'fa-expand' },
      plus: { color: 'btn-primary', icon: 'fa-plus' },
      minus: { color: 'btn-primary', icon: 'fa-minus' },
      send: { color: 'btn-success', icon: 'fa-paper-plane' },
      resetUser: { color: 'btn-success', icon: 'fa-times-circle' },
      drag: { color: 'btn-primary', icon: 'fa-arrows-alt' },
      widen: { color: 'btn-primary', icon: 'fa-arrows-h' },
    };

    return {
      restrict: 'E',
      scope: true,
      terminal: true,
      priority: 1,
      controllerAs: 'ffBtn',

      controller: function($scope) {
        const ffBtn = this;

        /**
         * Button click handler, tracks passed promise and runs/stops internal spinner
         */
        ffBtn.click = function(promise) {
          ffBtn.spin = true;
          return $q.when(promise).finally(() => { ffBtn.spin = false; });
        };

        /**
         * Auto-click button on countdown expiry
         */
        $scope.$on('ffCountdown::expiry', function($event) {
          $event.stopPropagation();
          $scope.$eval(ffBtn.clickExp);
        });
      },

      compile: function(tElement, tAttrs) {
        const title = tAttrs.title;
        tAttrs.$set('title', null);

        return function link($scope, $element, $attrs, ffBtn) {
          const origClick = $attrs.ffClick; // Backup original ffClick handler
          ffBtn.clickExp = $attrs.ffClick; // Click expression to be called outside of ngClick

          /**
           * If no ffSpin directive, add internal ffSpin which wraps the original click-handler call and tracks returned promise.
           * Terminates compilation, adds missing ffSpin attribute, then finishes compilation low-priority directives like ffSpin with -1.
           */
          if (!$attrs.ffSpin) {
            ffBtn.clickExp = 'ffBtn.click(' + $attrs.ffClick + ')';
            $attrs.$set('ffClick', ffBtn.clickExp);
            $attrs.$set('ffSpin', 'ffBtn.spin');
          }

          /**
           * Creating child input
           */
          const input = angular.element('<button class="ff-btn btn"></button>');
          const attrsToRemove = ffHelper.copyAttrs($element, input);
          $timeout(() => attrsToRemove.forEach(name => $element.attr(name, null)));

          $compile($element, null, 1)($scope);

          input.attr('title', title);
          input.attr('type', $attrs.type || 'button');
          input.addClass(sizeSizeClasses[$attrs.size] || 'btn-sm');

          // Button text
          if ($attrs.size !== 'xs') {
            input.append('<span>' + $element.html()  + '</span>');
          }

          $element.empty().append(input);
          $compile(input)($scope);

          $element.attr('ff-click', origClick); // Restore original click handler which might be substituted for ffSpin during compilation

          /**
           * Observer of sense attribute, manages button look-and-feel
           */
          $attrs.$observe('sense', function(sense) {
            input.removeClass('btn-primary btn-success btn-warning btn-danger');
            input.addClass(angular.isDefined(presets[sense]) ? presets[sense].color : 'btn-primary');

            // Button icon
            if (!presets[sense] || !!presets[sense].icon) {
              const icon = input[0].querySelector('.fa');
              if (icon) {
                icon.remove(); // Prevent multiple icons
              }

              input.append(angular.element('<span class="fa"></span>').addClass(presets[sense] ? presets[sense].icon : 'fa-check'));
            }

            // Exclude favorite button from tabindex to simulate disabled status
            input.attr('tabindex', (sense === 'favorite') ? -1 : 0);
          });

          /**
           * Emit ffBtn::spinStart/ffBtn::spinStop events for blocking neighbour buttons within ffBtnGroup directive
           */
          $scope.$watch($attrs.ffSpin, function(val) {
            $scope.$emit(val ? 'ffBtn::spinStart' : 'ffBtn::spinStop');
          });
        };
      },
    };
  });
