import './ng-validate-alert.scss';

/**
 * ngValidateAlert directive - validation error tooltips for FlexForms
 */
export default angular.module('flexForms.ngValidateAlert', [])

  .directive('ngValidateAlert', function($timeout, $translate, $filter) {
    return {
      restrict: 'A',
      require: ['ngValidateAlert', 'ngModel'],
      scope: false,
      controller: function() {},

      link: function($scope, $element, $attrs, controllers) {
        const $ctrl = controllers[0];
        const $model = controllers[1];

        let alertContainer;
        if ($element.parent().attr('ng-form')) { // control additionally internally wrapped to div[ng-form] e.g. ffDate
          alertContainer = $element.parent().parent().parent()[0].querySelector('ff-label');
        } else { // normal case
          alertContainer = $element.parent().parent()[0].querySelector('ff-label');
        }

        // if ff-label found, will use it, otherwise will use our parent
        alertContainer = alertContainer ? angular.element(alertContainer) : $element.parent();

        const alert = angular.element(document.createElement('div'))
          .addClass('ng-validate-alert')
          .toggleClass('top', $element.attr('type') === 'checkbox' || alertContainer[0].tagName.toLowerCase() !== 'ff-label')
          .attr('role', 'alert');

        alertContainer.append(alert);

        /**
         * Bind updateAlert when element receives focus
         */
        $element.on('focus', function() {
          $element.on($element.attr('type') === 'checkbox' ? 'change' : 'input', updateAlert);
          updateAlert();
        });

        /**
         * Unbind updateAlert when element looses focus, hide and clear the alert
         */
        $element.on('blur', function() {
          $element.off($element.attr('type') === 'checkbox' ? 'change' : 'input', updateAlert);
          alert.removeClass('visible').html('');
        });


        /**
         * Update Alert
         */
        function updateAlert() {
          $timeout(() => {
            if (!$model.$invalid || !$model.$touched && !$model.$dirty && !$model.$error.ext) return alert.removeClass('visible').html('');

            let options;
            try {
              options = $scope.$eval($attrs.ngValidateAlert); // assume options is object like { field: 'Test Field' }
            } catch (err) {}
            if (!options) options = { field: $attrs.ngValidateAlert }; // if not object, then string - field name

            const msg = getValidationMessage(options);

            if (msg) alert.addClass('visible').html(msg);
          }, 200);
        }

        /**
         * Get validation message
         */
        function getValidationMessage(options) {
          if ($model.$error.required)
            return options.required || $translate.instant('GLOBAL.VALIDATION.REQUIRED', options);

          if ($model.$error.date || $model.$error.parse && $element.hasClass('ff-date'))
            return options.date || $translate.instant('GLOBAL.VALIDATION.DATE', options);

          if ($model.$error.time || $model.$error.parse && $element.hasClass('ff-time'))
            return (options.time || $translate.instant('GLOBAL.VALIDATION.TIME', options)).replace('Date', 'Time');

          if ($model.$error.email)
            return options.email || $translate.instant('GLOBAL.VALIDATION.EMAIL', options);

          if ($model.$error.url)
            return options.url || $translate.instant('GLOBAL.VALIDATION.URL', options);

          if ($model.$error.minlength)
            return options.minlength || $translate.instant('GLOBAL.VALIDATION.MIN_LENGTH',
              { minLength: $attrs.ngMinlength || $attrs.minlength, field: options.field });

          if ($model.$error.maxlength)
            return options.maxlength || $translate.instant('GLOBAL.VALIDATION.MAX_LENGTH',
              { maxlength: $attrs.ngMaxlength || $attrs.maxlength, field: options.field });

          if ($model.$error.number)
            return options.number || $translate.instant('GLOBAL.VALIDATION.NUMBER', options);

          if ($model.$error.price)
            return options.price || $translate.instant('GLOBAL.VALIDATION.PRICE', options);

          if ($model.$error.pattern)
            return options.pattern;

          if ($model.$error.match)
            return options.match || $translate.instant('GLOBAL.VALIDATION.MATCH', options);

          if ($model.$error.cctype)
            return options.cctype || $translate.instant('GLOBAL.VALIDATION.CC_TYPE', $ctrl);

          if ($model.$error.ccnumber)
            return options.ccnumber || $translate.instant('GLOBAL.VALIDATION.CC_NUMBER', options);

          if ($model.$error.unique)
            return options.unique || $translate.instant('GLOBAL.VALIDATION.UNIQUE', options);

          if ($model.$error.lte) {
            let ngLte = $scope.$eval($attrs.ngLte);
            if (angular.isUndefined(ngLte)) ngLte = $attrs.ngLte;
            return options.lte || $translate.instant('GLOBAL.VALIDATION.LTE', { field: options.field, value: ngLte });
          }

          if ($model.$error.lt) {
            let ngLt = $scope.$eval($attrs.ngLt);
            if (angular.isUndefined(ngLt)) ngLt = $attrs.ngLt;
            return options.lt || $translate.instant('GLOBAL.VALIDATION.LT', { field: options.field, value: ngLt });
          }

          if ($model.$error.gte) {
            let ngGte = $scope.$eval($attrs.ngGte);
            if (angular.isUndefined(ngGte)) ngGte = $attrs.ngGte;
            return options.gte || $translate.instant('GLOBAL.VALIDATION.GTE', { field: options.field, value: ngGte });
          }

          if ($model.$error.gt) {
            let ngGt = $scope.$eval($attrs.ngGt);
            if (angular.isUndefined(ngGt)) ngGt = $attrs.ngGt;
            return options.gt || $translate.instant('GLOBAL.VALIDATION.GT', { field: options.field, value: ngGt });
          }

          if ($model.$error.dateOrder || $model.$error.timeOrder) {
            if (angular.isString(options.late))
              return $translate.instant('GLOBAL.VALIDATION.DATE_BEFORE', { field: options.field, value: options.late });
            if (angular.isString(options.early))
              return $translate.instant('GLOBAL.VALIDATION.DATE_AFTER', { field: options.field, value: options.early });
          }

          if ($model.$error.dateDiff) {
            if (angular.isString(options.late))
              return $translate.instant('GLOBAL.VALIDATION.DATE_DIFF',
                { field: options.field, refField: options.late, value: $filter('hours')($attrs.ngDateDiff) });
            if (angular.isString(options.early))
              return $translate.instant('GLOBAL.VALIDATION.DATE_DIFF',
                { field: options.field, refField: options.early, value: $filter('hours')($attrs.ngDateDiff) });
          }

          if ($model.$error.notContain)
            return options.notContain;

          if ($model.$error.font)
            return options.font || $translate.instant('GLOBAL.VALIDATION.FONT');

          if ($model.$error.custom)
            return $translate.instant(options.custom || options.field, options);
        }
      }
    };
  });
