/**
 * flex directive sets element flex style. E.g. flex="1 1 50%" -> style="flex: 1 1 50%"
 */
angular.module('flexForms.flex', [])

  .directive('flex', function(dynCss) { 'use strict';
    return {
      restrict: 'A',
      link: function ($scope, $element, $attrs) {
        $attrs.$observe('flex', function(param) {
          param = param.trim();
          if(param[0] !== '{') {
            param = `{ xs: '${param}' }`;
          }
          param = $scope.$eval(param);
          const classes = dynCss('flex', param).join(' ');
          $element.addClass(classes);
        });
      },
    };
  })

  // Converts directive name + parameter to list of CSS classes and dynamically creates these classes
  .factory('dynCss', function(dynCssBreakpoints) { 'use strict';
    const classes = {}; // class names cache

    return function(styleName, param) {
      return dynCssBreakpoints
        .map(bp => {
          const styleVal = param[bp.id];
          if(!styleVal) return null;
            const className = `${bp.id}_${styleName}_${styleVal.replace(/(?: |\.)/g, '_').replace(/%/g, 'pct')}`;
            if(angular.isUndefined(classes[className])) {
              classes[className] = bp.sheet.cssRules.length;
              bp.sheet.insertRule(`.${className} { ${styleName}: ${styleVal} !important; }`, bp.sheet.cssRules.length);
            }
            return className;
        })
        .filter(className => !!className);
    };
  })

  .factory('dynCssBreakpoints', function() { 'use strict';
    const breakpoints = [
      { id: 'xs', width: null },
      { id: 'sm', width: '480px' },
      { id: 'md', width: '640px' },
      { id: 'lg', width: '768px' },
      { id: 'xl', width: '1024px' },
    ];

    breakpoints.forEach(bp => {
      const style = document.createElement('style');
      if(bp.width) style.setAttribute('media', `(min-width: ${bp.width})`);
      // style.appendChild(document.createTextNode('')); // WebKit hack :(
      document.head.appendChild(style);
      bp.sheet = style.sheet;
    });

    return breakpoints;
  });

