import './ff-sort.scss'

/**
 * ffSort directive - FlexForms sort directive
 */
export default angular.module('flexForms.ffSort', [])
  .directive('ffSort', function() {
    return {
      restrict: 'E',
      scope: {
        colId: '<',
        props: '<',
        ffClick: '&'
      },
      template: '<span class="fa" ng-class="getClass()" ng-click="sort()" title="Click to sort"></span>',

      link: function($scope, $element) {
        $element.addClass('ff-spin-sm');

        $scope.sort = function() {
          if ($scope.props.sort === $scope.colId) {
            $scope.props.order = -$scope.props.order;
          } else {
            $scope.props.sort = $scope.colId;
            $scope.props.order = 1;
          }

          if (!$scope.spin) {
            $element.addClass('ff-spin');
            $scope.ffClick().finally(() => { $element.removeClass('ff-spin'); });
          }
        };

        $scope.getClass = function() {
          if ($scope.props.sort !== $scope.colId) return 'fa-sort';
          if ($scope.props.order < 0) return 'fa-sort-desc';
          return 'fa-sort-asc';
        };
      }
    };
  });
