/**
 * ffNumber directive - number input element. Allows integers/floats, negative values, short form of floats: -.5 .25 -,10 ,01
 */
export default angular.module('flexForms.ffNumber', [
  'deskworks.ffControlController',
  'deskworks.ffReadOnlyIgnore'
])

  .directive('ffNumber', function($compile, $templateCache, ffHelper) {
    return {
      restrict: 'E',
      scope: true,
      require: {
        ffBulkReadOnly: '^?ffBulkReadOnly',
        ffReadOnlyIgnore: '^?ffReadOnlyIgnore',
        ffBlock: '^^?ffBlock',
        ffControl: 'ffNumber',
      },
      controller: 'ffControlController as ffControl',
      bindToController: { },

      link: function($scope, $element, $attrs, $ctrls) {
        const ffControl = $ctrls.ffControl;
        const $input = angular.element($templateCache.get('ffText'));
        const $static = $compile($templateCache.get('ffTextStatic'))($scope);

        ffHelper.copyInnerAttrs($element, $attrs, $input);
        $element.empty().append($input);
        $compile($input)($scope);

        ffControl.init({ renderEdit: renderEdit, renderView: renderView, modelToStatic: modelToStatic });

        function renderEdit() {
          $static.detach();
          $element.append($input);
        }
        function renderView() {
          $input.detach();
          $element.append($static);
        }
        function modelToStatic(model) {
          return model;
        }

        /**
         * Parser which adds leading zeroes in model value to short floats .5 -.25 ,10 -,02
         */
        const ngModel = $input.controller('ngModel');
        ngModel.$parsers.push(value => {
          if (value) {
            const match = value.match(/^\s*-?(\d*)([,\.]?)\d+\s*$/i);
            if (match) {
              const integer = match[1];
              const delimiter = match[2];
              ngModel.$setValidity('number', true);

              if (delimiter) { // Check for integer part only if float number (0,5 -1.2 ,8 -.1)
                if (integer === '') {
                  value = value.replace(delimiter, '0.'); // -.5 -> -0.5  -,8 -> -0.8  .1 -> 0.1  ,2 -> 0.2
                } else {
                  value = delimiter === '.' ? value : value.replace(delimiter, '.'); // Replace delimiter from comma to dot
                }
              }
            } else {
              ngModel.$setValidity('number', false); // No match with pattern, invalid format
            }
          }
          const parsed = parseFloat(value);
          value = isNaN(parsed) ? null : parsed;
          return value; // Undefined/null value or no delimiter or no pattern match, return raw value
        });
      },
    };
  });
