import './ff-date.scss';

/**
 * ffDate directive - date input component
 */
export default angular.module('flexForms.ffDate', [
  'deskworks.ffControlController',
  'deskworks.ffReadOnlyIgnore'
])

  .directive('ffDate', function($compile, $templateCache, $locale, $filter, $window, $timeout, ffHelper) {
    return {
      restrict: 'E',
      scope: true,
      require: {
        ffBulkReadOnly: '^?ffBulkReadOnly',
        ffReadOnlyIgnore: '^?ffReadOnlyIgnore',
        ffBlock: '^^?ffBlock',
        ffControl: 'ffDate',
      },
      controller: 'ffControlController as ffControl',
      bindToController: { },

      link: function($scope, $element, $attrs, $ctrls) {
        const monthly = $scope.$eval($attrs.monthly);
        const $container = angular.element($templateCache.get('ffDate'));
        const $input = angular.element($container[0].querySelector('input'));
        const $static = $compile($templateCache.get('ffTextStatic'))($scope);

        if ($attrs.ffDisabled) angular.element($container[0].querySelector('button')).attr('ng-disabled', $attrs.ffDisabled);

        ffHelper.copyInnerAttrs($element, $attrs, $input);
        $element.empty().append($container);
        $compile($container)($scope);

        const ffControl = $ctrls.ffControl;
        ffControl.init({ renderEdit: renderEdit, renderView: renderView, modelToStatic: modelToStatic });

        function renderEdit() {
          $static.detach();
          $element.append($container);
        }
        function renderView() {
          $container.detach();
          $element.append($static);
        }
        function modelToStatic(model) {
          return monthly ? $filter('date')(model, 'MMM yyyy') : $filter('date')(model, 'mediumDate');
        }

        $scope.$attrs = $attrs;

        const $model = $input.controller('ngModel');

        /**
         * Formatter
         */
        let skipFormatter; // flag to skip one formatter call to prevent overwriting user input
        $model.$formatters.push(function formatter(value) {
          if (skipFormatter) { // skip one formatter call to prevent overwriting user input
            skipFormatter = false; // reset flag set by parser
            return $model.$viewValue; // just do nothing
          }
          return monthly ? $filter('date')(value, 'MMM yyyy') : $filter('date')(value, 'mediumDate');
        });

        /**
         * Parser
         */
        $model.$parsers.push(function parser(value) {
          if (!value) return value;
          const enMonths = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec',
            'January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];
          const lcMonths = $locale.DATETIME_FORMATS.SHORTMONTH.concat($locale.DATETIME_FORMATS.MONTH);
          const allMonths = lcMonths.concat(enMonths);
          const allNames = allMonths.join('|').replace(/\./g, '\\.?');

          let parsed = MMM_DD_YYYY(value) || DD_MMM_YYYY(value) || MM_DD_YYYY(value) || YYYY_MM_DD(value);
          if (monthly) parsed = parsed || MMM_YYYY(value) || MM_YYYY(value) || YYYY_MM(value);
          if (!parsed) return; // none of patterns matched

          const date = moment(parsed.value, parsed.format);
          if (date.isValid()) {
            $model.$setValidity('date', true);
            skipFormatter = true; // set flag to skip one formatter call to prevent overwriting user input
            if (monthly) date.date(1);
            return date;
          }
          $model.$setValidity('date', false);


          // 28 Mar 2015 or 28-Mar-2015 or 28 March 2015 or 28  2015 or 28 . 2015
          function DD_MMM_YYYY(value) {
            const rexp = new RegExp('^\\s*\\d{1,2}(?:\\s|-|\\.|\\/)+(' + allNames + ')(?:\\s|-|\\.|\\/)+\\d{4}\\s*$', 'i');
            const  match = value.match(rexp);

            if (match && match[1]) {
              let monthIndex = -1;
              for (let i = 0; i < allMonths.length; i++) {
                if (allMonths[i].toLowerCase().replace('.', '') === match[1].toLowerCase().replace('.', '')) {
                  monthIndex = i % 12;
                  break;
                }
              }
              value = value.replace(new RegExp(match[1].replace(/\./g, '\\.?'), 'i'), enMonths[monthIndex]);
              value = value.replace(/\s|-|\.|\//g, ' ').replace(/\s+/g, ' '); // replace delimiters to spaces

              return { format: 'DD MMM YYYY', value: value };
            }
          }

          // Mar 28, 2015 or Mar-28-2015 or March 28, 2015 or  28, 2015 or . 28, 2015
          function MMM_DD_YYYY(value) {
            const rexp = new RegExp('^\\s*(' + allNames + ')(?:\\s|-|\\.|\\/)+\\d{1,2}(?:\\s|-|\\.|\\/|,)+\\d{4}\\s*$', 'i');
            const match = value.match(rexp);

            if (match && match[1]) {
              let monthIndex = -1;
              for (let i = 0; i < allMonths.length; i++) {
                if (allMonths[i].toLowerCase().replace('.', '') === match[1].toLowerCase().replace('.', '')) {
                  monthIndex = i % 12;
                  break;
                }
              }
              value = value.replace(new RegExp(match[1].replace(/\./g, '\\.?'), 'i'), enMonths[monthIndex]);
              value = value.replace(/\s|-|\.|\/|,/g, ' ').replace(/\s+/g, ' '); // replace delimiters to spaces

              return { format: 'MMM DD YYYY', value: value };
            }
          }

          // 03/28/2015
          function MM_DD_YYYY(value) {
            if (value.match(/^\s*\d{1,2}(?:\s|-|\.|\/)+\d{1,2}(?:\s|-|\.|\/)+\d{4}\s*$/i)) {
              return {
                format: 'MM/DD/YYYY',
                value: value.replace(/\s|-|\./g, '/').replace(/\/+/g, '/'), // replace delimiters to slashes
              };
            }
          }

          // 2015-03-28
          function YYYY_MM_DD(value) {
            if (value.match(/^\s*\d{4}(?:\s|-|\.|\/)+\d{1,2}(?:\s|-|\.|\/)+\d{1,2}\s*$/i)) {
              return {
                format: 'YYYY-MM-DD',
                value: value.replace(/\s|-|\.|\//g, '-').replace(/-+/g, '-'), // replace delimiters to slashes
              };
            }
          }

          // Mar 2015 or Mar-2015 or March 2015 or  2015 or . 2015
          function MMM_YYYY(value) {
            const rexp = new RegExp('^\\s*(' + allNames + ')(?:\\s|-|\\.|\\/|,)+\\d{4}\\s*$', 'i');
            const match = value.match(rexp);

            if (match && match[1]) {
              let monthIndex = -1;
              for (let i = 0; i < allMonths.length; i++) {
                if (allMonths[i].toLowerCase().replace('.', '') === match[1].toLowerCase().replace('.', '')) {
                  monthIndex = i % 12;
                  break;
                }
              }
              value = value.replace(new RegExp(match[1].replace(/\./g, '\\.?'), 'i'), enMonths[monthIndex]);
              value = value.replace(/\s|-|\.|\/|,/g, ' ').replace(/\s+/g, ' '); // replace delimiters to spaces

              return { format: 'MMM YYYY', value: value };
            }
          }

          // 03/2015
          function MM_YYYY(value) {
            if (value.match(/^\s*\d{1,2}(?:\s|-|\.|\/)+\d{4}\s*$/i)) {
              return {
                format: 'MM/YYYY',
                value: value.replace(/\s|-|\./g, '/').replace(/\/+/g, '/'), // replace delimiters to slashes
              };
            }
          }

          // 2015-03
          function YYYY_MM(value) {
            if (value.match(/^\s*\d{4}(?:\s|-|\.|\/)+\d{1,2}\s*$/i)) {
              return {
                format: 'YYYY-MM',
                value: value.replace(/\s|-|\.|\//g, '-').replace(/-+/g, '-'), // replace delimiters to slashes
              };
            }
          }
        });

        /**
         * Close Datepicker handler
         */
        $scope.closeDP = function() {
          const dp = angular.element(document.getElementById('ff-dp-popup'));
          dp.remove(dp);
          $window.removeEventListener('click', onWindowClick);

          angular.element(document.querySelector('main')).css('padding-bottom', '');
        };

        /**
         * Open Datepicker handler
         */
        $scope.openDP = function($event) {
          $event.preventDefault();
          $event.stopPropagation();

          $scope.closeDP();
          $window.addEventListener('click', onWindowClick);

          let dp = angular.element('<ff-datepicker id="ff-dp-popup" tabindex="0" ng-click="stopPropagation($event)"></ff-datepicker>');
          dp.attr('ff-model', $attrs.ffModel);
          dp.attr('date-min', $attrs.dateMin);
          dp.attr('date-max', $attrs.dateMax);
          dp.attr('date-disabled', $attrs.dateDisabled);
          dp.attr('monthly', $attrs.monthly);
          dp = $compile(dp)($scope);
          $element.append(dp);
          dp[0].focus();

          $timeout(() => {
            dp[0].querySelector('.ff-dp-cal-body').addEventListener('click', function() { // close datepicker on date selection
              $scope.closeDP();
              $timeout(() => { // return focus to ff-date > input element on date selection
                $element[0].querySelector('input').focus();
              });

              for (let key in $scope.ffDateForm) { // set date element to dirty state
                if ($scope.ffDateForm.hasOwnProperty(key) && key.indexOf('$') < 0) {
                  if (angular.isString($scope.ffDateForm[key].$name)) {
                    $scope.ffDateForm[key].$setDirty();

                    angular.forEach($scope.ffDateForm[key].$viewChangeListeners, function(listener) {
                      listener(); // notify all ngChange listeners
                    });
                  }
                }
              }
            });

            // extend height of main element if datepicker overflows it
            const main = document.querySelector('main');
            const paddingBottom = parseInt($window.getComputedStyle(main).paddingBottom);
            angular.element(main).css('padding-bottom', paddingBottom + main.scrollHeight - main.clientHeight + 50 + 'px');
          });
        };

        $scope.stopPropagation = function($event) {
          $event.preventDefault();
          $event.stopPropagation();
        };

        $scope.$on('$destroy', function() {
          $scope.closeDP();
        });

        function onWindowClick() {
          $scope.$apply('closeDP()');
        }
      },
    };
  });
