/**
 * ffPanelHeader directive transcludes to ffPanel header
 */
export default angular.module('flexForms.ffPanelHeader', [])
  .directive('ffPanelHeader', function(helper) {
    return {
      restrict: 'E',
      transclude: 'element',

      link: function($scope, $element, $attrs, $ctrl, $transclude) {
        $transclude(clone => {
          const panel = helper.findParentElement($element, e => e.hasClass('ff-panel'));
          if (panel) panel.prepend(clone.addClass('ff-panel-header').attr('role', 'heading').attr('aria-level', '2'));
        });
      }
    };
  })

  .directive('ffHeader', function(helper) {
    return {
      restrict: 'EC',

      compile: (tElement) => {
        tElement.attr('role', 'heading').attr('aria-level', '2');
        return () => {};
      },
    };
  });
