import './ff-pagination.scss'

/**
 * ffPagination directive - FlexForms pagination directive
 */
angular.module('flexForms.ffPagination', [])
  .directive('ffPagination', function() { 'use strict';
    return {
      restrict: 'E',
      scope: {
        props: '=',
        ffClick: '&'
      },
      template: '<span ng-repeat="p in range(props.pageCount) track by $index" ff-spin="spin == $index + 1"' +
        ' ng-class="{ active: ($index + 1) == props.page }" ng-click="page($index + 1)">{{$index + 1}}</span>',

      link: function($scope) {
        $scope.range = size => new Array(size);

        $scope.page = function(page) {
          if(!$scope.spin && page != $scope.props.page) {
            $scope.spin = page;
            $scope.ffClick({ $page: page }).finally(function() { $scope.spin = 0; });
          }
        };
      }
    };
  });
