import './ff-tip.scss'

/**
 * ffTip directive - tooltip which appears on hover of .ff-tip-trigger element.
 */
export default angular.module('flexForms.ffTip', [])

  .directive('ffTip', function($window, $timeout, helper) {
    return {
      restrict: 'EAC',

      compile: function(tElement, tAttrs) {
        tElement.addClass('ff-tip');
        tElement.addClass(tAttrs.tipType);

        const inner = angular.element('<div class="ff-tip-inner"></div>').append(tElement.html());
        tElement.empty().append(inner);
        const triangle = angular.element('<i></i>');
        tElement.append(triangle);

        /**
         * Link
         */
        return function link($scope, $element, $attrs) {
          const triangle = angular.element($element[0].querySelector('i'));
          let trigger = $element[0].nodeName === 'FF-TIP' ? $element.parent() : $element;
          trigger = helper.findParentElement($element, e => e.hasClass('ff-tip-trigger')) || trigger;
          const tipHover = $attrs.tipHover;
          const rcScroller = angular.element($element[0].querySelector('.rc-tip-scroller'));

          trigger.addClass('ff-tip-trigger');
          trigger.on('mouseenter', onMouseEnter);

          $scope.$on('$destroy', function() {
            trigger.off('mouseenter', onMouseEnter);
          });

          $scope.$on('ffTip:layout', () => {
            if ($element.hasClass('ff-tip-right')) {
              $element.css('margin-top', 0);
              $window.requestAnimationFrame(() => positionRight(5)); // fire during next 5 animation frames
            } else {
              $element.toggleClass('ff-tip-bottom', trigger[0].getBoundingClientRect().top < parseInt($window.getComputedStyle($element[0]).height));
              $element.css('margin-left', 0);
              triangle.css('margin-left', 0);
              $window.requestAnimationFrame(() => positionTopBottom(5)); // fire during next 5 animation frames
            }
          });

          /**
           * Trigger mouse enter handler, adds class .bottom if tip doesn't fit to screen above container
           */
          function onMouseEnter() {
            if (tipHover) $scope.$eval(tipHover);

            if ($element.hasClass('rc-marker-info')) {
              $element.css('margin-left', 0);
              triangle.css('margin-left', 0);
              $timeout(() => { // prevent right-cropping tooltip
                const rect = $element[0].getBoundingClientRect();
                const tRect = triangle[0].getBoundingClientRect();
                let marginLeft = parseInt($window.getComputedStyle($element[0]).marginLeft);
                marginLeft = Math.min(marginLeft - (rect.right - $window.innerWidth), 0);
                $element.css('margin-left', marginLeft + 'px');
                triangle.css('margin-left', Math.min(-marginLeft, (rect.width - tRect.width) / 2)  + 'px');
              }, 100);
            }

            if ($element.hasClass('ff-tip-right')) {
              $element.css('margin-top', 0);
              $window.requestAnimationFrame(() => positionRight(10)); // fire during next 10 animation frames
            } else {
              $element.toggleClass('ff-tip-bottom', trigger[0].getBoundingClientRect().top < parseInt($window.getComputedStyle($element[0]).height));
              $element.css('margin-left', 0);
              triangle.css('margin-left', 0);
              $window.requestAnimationFrame(() => positionTopBottom(10)); // fire during next 10 animation frames
            }
          }

          function positionRight(framesCount) {
            const rect = $element[0].getBoundingClientRect();
            let marginTop;

            const vpTop = document.querySelector('rc-header').getBoundingClientRect().bottom + 5;
            const vpBottom = document.documentElement.clientHeight - 5;

            if (rect.top < vpTop || rect.height > (vpBottom - vpTop)) {
              marginTop = parseInt($element.css('margin-top'));
              // var marginTop = parseInt($window.getComputedStyle($element[0]).getPropertyValue('margin-top'));
              marginTop += vpTop - rect.top;
              $element.css('margin-top', marginTop + 'px');
              triangle.css('margin-top', -Math.min(marginTop, rect.height / 2 - 12) + 'px');
            }
            else if (rect.bottom > vpBottom) {
              marginTop = parseInt($element.css('margin-top'));
              marginTop += vpBottom - rect.bottom;
              $element.css('margin-top', marginTop + 'px');
              triangle.css('margin-top', Math.min(-marginTop, rect.height / 2 - 12) + 'px');
            }
            rcScroller.css('max-height', (vpBottom - vpTop - 40) + 'px');

            if (framesCount > 0) $window.requestAnimationFrame(() => positionRight(framesCount - 1));
          }

          function positionTopBottom(framesCount) {
            const rect = $element[0].getBoundingClientRect();
            let marginLeft;

            const boundingRect = document.querySelector('main').getBoundingClientRect();
            const vpLeft = boundingRect.left + 5;
            const vpRight = boundingRect.right - 5;


            if (rect.left < vpLeft) {
              marginLeft = parseInt($element.css('margin-left'));
              marginLeft += vpLeft - rect.left;
              $element.css('margin-left', marginLeft + 'px');
              triangle.css('margin-left', -Math.min(marginLeft, rect.width / 2 - 12) + 'px');
            }
            else if (rect.right > vpRight) {
              marginLeft = parseInt($element.css('margin-left'));
              marginLeft += vpRight - rect.right;
              $element.css('margin-left', marginLeft + 'px');
              triangle.css('margin-left', Math.min(-marginLeft, rect.width / 2 - 12) + 'px');
            }

            if (framesCount > 0) $window.requestAnimationFrame(() => positionTopBottom(framesCount - 1));
          }
        };
      }
    };
  });
