/**
 * ffSpin directive (loading progress indicator)
 */
export default angular.module('flexForms.ffSpin', [])
  .directive('ffSpin', function() {
    return {
      restrict: 'A',
      priority: -1,
      link: function ($scope, $element, $attrs) {
        $scope.$watch($attrs.ffSpin, newVal => {
          const input = $element[0].querySelector('button');
          $element.toggleClass('ff-spin', !!newVal);
          if (input) angular.element(input).toggleClass('disabled', !!newVal);
        });
      }
    };
  });
