/**
 * ngCustom directive for custom validator
 */
export default angular.module('flexForms.validation.ngCustom', [])
  .directive('ngCustom', function() {
    return {
      restrict: 'A',
      require: '?ngModel',

      link: function($scope, $element, $attrs, ngModel) {
        if (!ngModel || !$attrs.ngCustom) { return; }

        const validator = ($value) => {
          const result = $scope.$eval($attrs.ngCustom, { $value });
          ngModel.$setValidity('custom', result);
          return $value;
        };

        ngModel.$parsers.push(validator);

        $scope.$watch($attrs.ngCustom, () => validator(ngModel.$viewValue));
      }
    };
  });
