/**
 * ffIncludeAll directive - 'Include All' checkbox with embedded select/deselect all items behavior
 */
angular.module('flexForms.ffIncludeAll', [])

  .directive('ffIncludeAll', function() { 'use strict';
    const template = '<ff-checkbox class="ff-center ff-include-all" ff-model="inc.all" ff-click="inc.selectAll()"' +
      ' ff-disabled="!inc.items || !inc.items.length" title="Include All" flex="1 1 auto">{{\'GLOBAL.INCLUDE_ALL\' | translate}}</ff-checkbox>';

    return {
      restrict: 'E',
      scope: true,
      template: template,
      controller: 'IncludeAllCtrl as inc',
      bindToController: {
        items: '<',
        default: '<',
      }
    };
  })

  .controller('IncludeAllCtrl', function($scope, $timeout) { 'use strict';
    const inc = this;

    /**
     * Include All checkbox handler
     */
    function selectAll() {
      angular.forEach(inc.items, item => { item.include = inc.all; });
    }

    inc.selectAll = () => $timeout(selectAll);

    const unwatch = $scope.$watchCollection('inc.items', newVal => {
      if (newVal) {
        inc.all = !!inc.default;
        selectAll();
        unwatch();
      }
    });

    /**
     * Watch for items include status and update Include All checkbox status
     */
    $scope.$watchCollection(() => inc.items && inc.items.map(item => item.include), () => {
      inc.all = inc.items && inc.items.length && inc.items.every(item => !!item.include) || false;
    });
  });
