/**
 * ffRadio directive - wrapper for standard input type="radio"
 */
angular.module('flexForms.ffRadio', [
  'deskworks.ffControlController',
  'deskworks.ffReadOnlyIgnore',
])

  .directive('ffRadio', function($compile, $templateCache, ffHelper) { 'use strict';
    return {
      restrict: 'E',
      scope: true,
      require: {
        ffBulkReadOnly: '^?ffBulkReadOnly',
        ffReadOnlyIgnore: '^?ffReadOnlyIgnore',
        ffBlock: '^^?ffBlock',
        ffControl: 'ffRadio',
      },
      controller: 'ffControlController as ffControl',
      bindToController: { },

      link: ($scope, $element, $attrs, $ctrls) => {
        const ffControl = $ctrls.ffControl;
        const $input = angular.element($templateCache.get('ffRadio'));
        const $i = angular.element('<i></i>');
        const $static = $compile($templateCache.get('ffRadioStatic'))($scope);

        let $label = $attrs.ffLabel || $element.html();
        if ($label) $label = angular.element('<span class="cb-label"></span>').append($label);

        ffHelper.copyInnerAttrs($element, $attrs, $input);
        $element.empty().append($input).append($i);
        $compile($input)($scope);

        if ($label) {
          $element.append($label);
          $compile($label)($scope);
        }

        ffControl.init({ renderEdit: renderEdit, renderView: renderView, modelToStatic: modelToStatic });

        function renderEdit() {
          $static.detach();
          $element.prepend($input);
        }
        function renderView() {
          $input.detach();
          $element.prepend($static);
        }
        function modelToStatic(model) {
          return $scope.$eval($attrs.ffValue) === model;
        }
      },
    };
  });
