import cuid from 'shared/uids/cuid.service';

/**
 * ffPanel directive - replacement for flexForm directive.
 */
export default angular.module('flexForms.ffPanel', [
  cuid.name,
  'flexForms.ffPanelHeader',
])
  .directive('ffPanel', function(cuid) {
    return {
      restrict: 'EAC',

      compile: function(tElement) {
        const tpl = '<div class="ff-panel-body">' + tElement.html() + '</div>';

        tElement.addClass('ff-panel');
        tElement.html(tpl);

        return function link($scope, $element) {
          if (!$element.attr('aria-label') && !$element.attr('aria-labelledby')) {
            const header = $element[0].querySelector('ff-panel-header') || $element[0].querySelector('ff-header');
            if (header) {
              const label = header.querySelector('[translate]') || header;
              if (!label.id) label.id = cuid();
              $element.attr('aria-labelledby', label.id);
              $element.attr('role', 'article');
            }
          }
        };
      },
    };
  });
